@import url(https://fonts.googleapis.com/css?family=Roboto:100i,300,300i,400,400i,500,500i,700,700i,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.container {
  width: 1300px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  display: flex;
  justify-content: center; }

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  margin: 100px 0 50px; }
  .search-form__search {
    width: 400px;
    height: 40px;
    border: 5px solid #FEBB02;
    padding-left: 5px;
    font-size: 18px; }
    .search-form__search:focus {
      outline: none; }
  .search-form__date {
    width: 150px;
    height: 40px;
    border: 5px solid #FEBB02;
    border-left: none;
    padding-left: 5px; }
    .search-form__date:focus {
      outline: none; }
  .search-form__button {
    width: 70px;
    height: 52px;
    border: 5px solid #FEBB02;
    border-left: none;
    cursor: pointer;
    background: #0077CC; }
    .search-form__button:focus {
      outline: none; }
    .search-form__button:hover {
      background: #0066af; }
    .search-form__button:active {
      background: #FEBB02; }
      .search-form__button:active .icon-comment {
        color: #000; }
    .search-form__button .icon-comment {
      color: #fff; }

.box-one,
.box-two,
.box-three {
  display: flex;
  font-family: 'Open Sans', sans-serif; }
  .box-one__item,
  .box-two__item,
  .box-three__item {
    position: relative;
    margin: 0 2px; }
  .box-one__img,
  .box-two__img,
  .box-three__img {
    width: 100%; }
  .box-one__name,
  .box-two__name,
  .box-three__name {
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 24px;
    font-weight: bold;
    color: #fff; }

.hotels__item {
  margin-bottom: 10px;
  height: 230px; }

.hotel {
  display: flex;
  justify-content: space-between;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 0 20px; }
  .hotel__info {
    width: 100%;
    margin: 10px 0; }
  .hotel__name {
    font-size: 24px;
    margin: 10px 0 10px; }
  .hotel__stars {
    margin: 0 10px 5px 0; }
  .hotel__text-date {
    margin-top: 10px;
    font-size: 14px; }
  .hotel__photo {
    margin: 10px 0;
    width: 430px;
    display: flex;
    justify-content: center;
    align-items: center; }

.weather {
  display: flex; }
  .weather__day {
    margin: 0 10px;
    text-align: center; }
  .weather__date {
    margin: 10px 0;
    font-size: 14px; }

.wu {
  display: inline-block;
  background-repeat: no-repeat;
  padding: 0;
  margin: 0; }

/*! Setup Default Sizes */
